import React, { useEffect, useState } from 'react';
import SignUpInput from './SignInput';
import SignupPhoneCertification from './SignupPhoneCertification';
import { signupCheckId } from '../services/customer';

const SignUpField = () => {
  const [userId, setUserId] = useState('');
  const [password, setPassword] = useState('');
  const [passwordConfirm, setPasswordConfirm] = useState('');
  const [userName, setuserName] = useState('');
  const [email, setEmail] = useState('');
  const [phone, setPhone] = useState('');
  const [errorMsg, setErrorMsg] = useState('');
  const [error, setError] = useState(false);

  // const [phoneConfirm, setPhoneConfirm] = useState("")

  const [submit, setSubmit] = useState(false);

  useEffect(() => {
    // 
    password && passwordConfirm && password !== passwordConfirm
      ? setErrorMsg('  .')
      : setErrorMsg('');

    errorMsg !== '' ? setError(true) : setError(false);
  });

  const handleUserId = (e) => {
    let value = e.target.value;
    //,   .
    value = value.replace(/[^A-Za-z0-9]/gi, '');
    setUserId(value);
  };
  const handlePassword = (e) => {
    let value = e.target.value;
    //, ,    .
    value = value.replace(/[^a-zA-Z0-9!@#$%^&*=+]/gi, '');
    setPassword(value);
  };
  const handlePasswordConfirm = (e) => {
    let value = e.target.value;
    //, ,    .
    value = value.replace(/[^a-zA-Z0-9!@#$%^&*=+]/gi, '');
    setPasswordConfirm(value);
  };
  const handleUserName = (e) => {
    let value = e.target.value;
    //,   .
    value = value.replace(/[^A-Za-z][^--]/gi, '');
    setuserName(value);
  };
  const handleEmail = (e) => {
    setEmail(e.target.value);
  };
  const checkEmail = (e) => {
    let emailcheck =
      /^[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]{2,3}$/gi;
    if (emailcheck.test(e.target.value)) setEmail(e.target.value);
    else setEmail('');
  };
  const handlePhone = (e) => {
    let value = e.target.value;
    value = value.replace(/[^0-9]/gi, '');
    setPhone(value);
  };

  // 
  const checkId = () => {
    const cid = userId;
    if (cid === '') {
      alert(' !');
      return;
    }
    if (signupCheckId(cid)) {
      alert('  ');
      setUserId(cid);
    } else {
      alert(' .   .');
      setUserId('');
    }
  };

  return (
    <div className="field-div">
      <SignUpInput
        type="text"
        id="uid"
        name="userId"
        value={userId}
        handleChange={handleUserId}
        labelTxt=" (,   20)"
        max="20"
        isRequired={true}
        checkId={checkId}
      />
      <SignUpInput
        type="password"
        id="pw"
        name="password"
        value={password}
        handleChange={handlePassword}
        labelTxt=" (, ,   8-15)"
        max="15"
        auto="off"
        isRequired={true}
      />
      <SignUpInput
        type="password"
        id="pwC"
        name="passwordConfirm"
        value={passwordConfirm}
        handleChange={handlePasswordConfirm}
        labelTxt=" "
        max="15"
        auto="off"
        isRequired={true}
      />
      {error && (
        <div className="error">
          <span className="error-txt">
            <br />
            {errorMsg}
          </span>
        </div>
      )}

      <SignUpInput
        type="text"
        id="uName"
        name="userName"
        value={userName}
        handleChange={handleUserName}
        labelTxt=""
        max=""
        auto="off"
        isRequired={true}
      />
      <SignUpInput
        type="email"
        id="em"
        name="email"
        value={email}
        handleChange={handleEmail}
        handleBlur={checkEmail}
        labelTxt=""
        max=""
        isRequired={true}
      />
      <SignUpInput
        type="text"
        id="pnum"
        name="phone"
        value={phone}
        handleChange={handlePhone}
        labelTxt="('-' )"
        max="11"
        isRequired={true}
      />

      {/*     */}
      {/* <SignupPhoneCertification /> */}
    </div>
  );
};

export default SignUpField;
