import axios from 'axios';
import { Link } from 'react-router-dom';

function signup(ele) {
  if (ele.password !== ele.passwordConfirm) {
    alert('  .');
    return;
  }
  axios
    .post(`${process.env.REACT_APP_SERVER}/db/customer/insert`, getParam(ele))
    .then((res) => {
      console.log(res);
      if (alert(' .')) return <Link to="/login" />;
    })
    .catch((err) => {
      console.log(err);
      alert(
        '    .    .',
      );
    });
}

const getParam = (d) => {
  return new URLSearchParams({
    userId: d.userId.value,
    password: d.password.value,
    userName: d.userName.value,
    email: d.email.value,
    phone: d.phone.value,
    agreeMarketing: d.agreeMarketing.checked ? 1 : 0,
  });
};

function signupCheckId(id) {
  axios
    .get(`${process.env.REACT_APP_SERVER}/db/customer/checkId`, {
      params: { userId: id },
    })
    .then((res) => {
      // console.log(res)
      if (res.data.length === 0) {
        // alert("  ")
        return true;
      } else {
        // alert(" .");
        return false;
      }
    })
    .catch((e) => {
      console.log(e);
      alert('     .');
    });
}
export { signup, signupCheckId };
