import React, { useState } from 'react';
import styled, { css } from 'styled-components';
import Button from '../common/Button';
import { Link } from 'react-router-dom';

//     .

const AuthFormBlock = styled.div`
  h3 {
    margin: 0;
    font-size: 25pt;
    margin-bottom: 3rem;
  }
`;
// styled input
const StyledInput = styled.input`
  font-size: 1rem;
  border: 1px solid #8e8e8e;
  border-radius: 30px;
  width: 100%;
  height: 45px;
  outline: none;
  padding-left: 1.5rem;
  margin-bottom: 0.5rem;
  &:focus {
    color: #1a56ce;
    border: 1px solid #1a56ce;
  }
  & + & {
    // margin-top: 0.8rem;
  }
`;

const ViewPassword = styled.span`
  position: relative;
  cursor: pointer;
`;

const ButtonWithMarginTop = styled(Button)`
  margin-top: 1rem;
`;

const Footer = styled.div`
  margin-top: 2rem;
  // text-align: right;
  a {
    color: #555;
    text-decoration: underline;
    &:hover {
      color: #1a56ce;
    }
  }
`;

const textMap = {
  login: '',
  register: '',
};
/**
 *  
 */
const ErrorMessage = styled.div`
  color: red;
  text-align: center;
  font-size: 0.875rem;
  margin-top: 1rem;
`;

const AuthForm = ({ type, form, onChange, onSubmit, error }) => {
  const [passwordType, setPasswordType] = useState('password');
  const [passwordConfirmType, setPasswordConfirmType] = useState('password');

  const text = textMap[type];

  const togglePassword = () => {
    if (passwordType === 'password') {
      setPasswordType('text');
      return;
    }
    setPasswordType('password');
  };
  const togglePasswordConfirm = () => {
    if (passwordConfirmType === 'password') {
      setPasswordConfirmType('text');
      return;
    }
    setPasswordConfirmType('password');
  };

  return (
    <AuthFormBlock>
      <h3>{text}</h3>
      <form onSubmit={onSubmit}>
        <StyledInput
          autoComplete="userEmail"
          name="userEmail"
          placeholder=""
          onChange={onChange}
          value={form.userEmail}
        />
        <StyledInput
          autoComplete="new-password"
          name="password"
          placeholder=""
          type={passwordType}
          onChange={onChange}
          value={form.password}
        />
        <ViewPassword onClick={togglePassword}>
          {passwordType === 'password' ? (
            <i className="bi pw-hidden"></i>
          ) : (
            <i className="bi pw-view"></i>
          )}
        </ViewPassword>

        {type === 'register' && (
          <>
            <StyledInput
              autoComplete="new-password"
              name="passwordConfirm"
              placeholder=" "
              type={passwordConfirmType}
              onChange={onChange}
              value={form.passwordConfirm}
            />
            <ViewPassword onClick={togglePasswordConfirm}>
              {passwordConfirmType === 'password' ? (
                <i className="bi pw-hidden"></i>
              ) : (
                <i className="bi pw-view"></i>
              )}
            </ViewPassword>
          </>
        )}
        {error && <ErrorMessage>{error}</ErrorMessage>}

        <ButtonWithMarginTop $fullwidth={true}>
          {type === 'login' ? text : ''}
        </ButtonWithMarginTop>
      </form>
      <Footer>
        {type === 'login' ? (
          <Link to="/register"></Link>
        ) : (
          <Link to="/login"></Link>
        )}
      </Footer>
    </AuthFormBlock>
  );
};

export default AuthForm;
